body {
    margin: 0;
    padding: 0;
    background: url('http://sebsauvage.net/galerie/photos/Wallpapers/Wallbase.cc/wallpaper-668504.jpg'), #092887;
    background-size: cover;
    width: 100%;
    height: 100vh;
}

* {
    font-family: Helvetica, sans-serif,Arial;
    font-weight: 100;
}

hr {
    color: #222;
    background-color: #222;
    border-color: #222;
}

.flex {
    display: -ms-flexbox;
    display: flex;
}

.vertical {
    -ms-flex-direction: column;
        flex-direction: column;
}

.flex-center {
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.flex-1 {
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 10px 0;
}

.full-width {
    width: 100%;
}

.App {
    background: rgba(5, 50, 96, 0.545);
    width: 100%;
    height: 100vh;
    min-height: 350px;
}

.Container {
    background-color: rgba(240, 240, 240, 0.500);
    border-radius: 5px;
    padding: 10px;
    margin: 5px;
}
@media screen and (max-width: 720px){
    .Container {
        border-radius: 0px;
        padding: 10px;
        margin: 5px 0;
        width: calc(100% - 20px);
    }
}

.title {
    color: #303030;
    text-align: center;
    font-size: 2rem;
}

input {
    padding: 5px 10px;
    border-radius: 5px;
    border-width: 0px; 
    font-size: 1.2rem;
    margin: 10px 5px; 
}

select {
    padding: 5px 10px;
    border-radius: 5px;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-width: 0px; 
    font-size: 1rem;
}

a {
    text-decoration: none;
    color: #092887;
}


